import {
  BodyStyle,
  LeftSideMenu,
  Items,
  Banner,
  Title,
  Image,
  MenuTitle,
  MenuTitleItem
} from './styles'

class Body extends React.Component {

  constructor(){
    super();

    this.state = {
      category: false
    };

    this.changeCategoryField = this.changeCategoryField.bind(this);
  };
  
  changeCategoryField = () => {
    this.setState({
      category: !this.state.category
    })
  }
  render(){
    return (
      <BodyStyle>
          <LeftSideMenu onClick={this.changeCategoryField}>
          <MenuTitle>АКЦИИ И ПРЕДЛОЖЕНИЯ</MenuTitle>
            {this.state.category ? (
              <MenuUl>
                <MenuLiF>ТЕЛЕФОНЫ И АКСЕССУАРЫ</MenuLiF>
                <MenuLi>Смартфоны</MenuLi>
                <MenuLi>Смартфоны</MenuLi>
                <MenuLi>Смартфоны</MenuLi>
                <MenuLi>Смартфоны</MenuLi>
              </MenuUl>
            ):
            (
              null
            )
            }
          </LeftSideMenu>
          <Items>
            <Banner>
              <Title>
                ЭЛЕКТРОНИКА
              </Title>
              <Image>
                Apple AirPods <br></br>
                со скидкой 25%
              </Image>
            </Banner>
          </Items>
      </BodyStyle>
    )
  }
}

export default Body